.top {
  margin: 155px auto 0;
  display: flex;
  justify-content: space-between;
  .left {
    width: 53px;
    height: 112px;
    background: url('../../assets/imgs/imgs/left.svg') center / cover no-repeat;
  }
  .centertext {
    font-style: normal;
    font-weight: 700;
    line-height: 112px;
    letter-spacing: -1px;
    text-align: center;
    color: #1e1e1e;
    padding-left: 35px;
    padding-right: 35px;
  }
  .right {
    width: 53px;
    height: 112px;
    background: url('../../assets/imgs/imgs/right.svg') center / cover no-repeat;
  }
}
.toptext {
  width: 644px;
  height: 56px;
  margin: 30px auto 0;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0px;
  text-align: center;
  color: #1e1e1e;
}
