.footer {
  width: 100%;
  height: 400px;
  background: #202020;

  .container {
    width: 1140px;
    margin: 0 auto;
    padding-top: 97px;
    display: flex;
    justify-content: space-between;
    .block:last-child {
      width: 360px;
    }
    .title {
      width: 165px;
      height: 25px;
      font-style: normal;
      font-weight: 700;
      font-size: 15px;
      color: white;
      margin-bottom: 20px;
      cursor: pointer;
      &:hover {
        color: #ec407a;
      }
      a {
        color: white;
        &:hover {
          color: #ec407a;
        }
      }
    }
    .logo {
      width: 172px;
      height: 26px;
      background: url(../../assets/imgs/imgs/logo_white.png) no-repeat center
        center;
      background-size: 100% 100%;
      margin-bottom: 20px;
    }
    .text {
      display: block;
      width: 165px;
      height: 40px;
      line-height: 20px;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      color: #f0f0f0;
      cursor: pointer;
      &:hover {
        color: #ec407a;
      }
    }
    .icon {
      width: 24px;
      height: 24px;
      &:hover {
        opacity: 0.8;
      }
      ~ .icon {
        margin-left: 18px;
      }
    }
  }

  @media (max-width: 768px) {
    height: auto;

    .container {
      width: 92%;

      .links-container {
        display: block;
        height: auto;
        padding: 20px 0;

        .logo {
          width: 138px;
          height: 20px;
          background: url(../../assets/imgs/imgs/logo_white.png) no-repeat
            center center;
          background-size: 100% 100%;
        }

        .links {
          margin: 15px 0 0;

          .link-item {
            margin: 0 30px 0 0;
            font-size: 14px;
            line-height: 30px;
          }
        }
      }

      .copyright {
        height: 83px;
        justify-content: flex-start;
      }
    }
  }
}
