.aboutblackbg {
  background-color: #1d1d1d;
  margin: auto;
  width: 1903px;
  height: 420px;
  padding-top: 88px;
}

.aboutbtn {
  margin-top: 50px;
  width: 300px;
  height: 36px;
  line-height: 32px;
  text-align: center;
  font-weight: bold;
  border: 2px solid #ec407a;
  border-radius: 50px;
  cursor: pointer;
  &:hover {
    border: 2px solid;
    opacity: 0.8;
  }
}
.aboutwant {
  width: 1500px;
  margin: auto;
  padding: 120px;
  display: flex;
  justify-content: space-between;

  .text {
    width: 450px;
    margin-top: 80px;
    line-height: 50px;
    > :nth-child(2) {
      margin-top: 30px;
    }
  }
}
.aboutPinkbg {
  width: 100%;
  height: 300px;
  background-color: #ec407a;
}
