.data-center {
  .highlight-container {
    margin-top: 80px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @media screen and (max-width: 768px) {
      > div {
        margin-top: 60px;
        &:first-child {
          margin-top: 0;
        }
      }
    }
    div:last-child {
      margin-bottom: 0;
    }
  }
}
