.card-wrap {
  @media screen and (max-width: 768px) {
    flex-wrap: wrap;
  }
  display: flex;
  justify-content: space-between;
  > div {
    @media screen and (max-width: 768px) {
      width: calc(100% - 30px);
      margin-left: 15px;
      margin-right: 15px;
      &::before {
        content: none !important;
      }
    }
    width: 288px;
    height: 560px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 20px;
    color: #3f3f3f;
    background: #fff;
    margin-top: 60px;
    // cursor: pointer;
    > div,
    button {
      position: relative;
      z-index: 99;
    }
    &::before {
      content: '';
      position: absolute;
      width: 304px;
      height: 576px;
      left: -8px;
      top: -8px;
      border: 4px solid rgba(128, 128, 128, 0.205);
      border-radius: 26px;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
      z-index: 33;
    }
    &:hover {
      &::before {
        opacity: 1;
      }
    }
    .row {
      width: 80%;
      height: 54px;
      line-height: 54px;
      text-align: center;
      border-top: 0.5px solid #c4c4c4;
    }
    button {
      width: 192px;
      height: 36px;
      border-radius: 50px;
      margin-top: 25px;
    }
    > div {
      text-align: center;
    }
  }
  .shadow {
    box-shadow: 0px 0px 5px 5px rgba(128, 128, 128, 0.205);
  }
}
