.point-wrap {
  // width: 1100px;
  margin: 60px auto 0;
  @media screen and (max-width: 768px) {
    flex-wrap: wrap;
    > div {
      margin-top: 60px;
      &:first-child {
        margin-top: 0;
      }
    }
  }
  display: flex;
  justify-content: space-evenly;
  position: relative;
}
