.web-content-wrap {
  width: 950px;
  display: flex;
  flex-wrap: wrap;
  margin: 60px auto;
  position: relative;
  > div {
    @media screen and (max-width: 768px) {
      width: 100% !important;
      margin: 55px 0 0 !important;
      &:first-child {
        margin: 0 !important;
      }
    }
    width: 40%;
    &:first-child,
    &:nth-child(2) {
      margin-bottom: 45px;
    }
    &:nth-child(2n) {
      width: 45%;
      margin-left: 12%;
    }
    > div {
      &:first-child {
        @media screen and (max-width: 768px) {
          flex-direction: column;
          align-items: center;
          justify-content: center;
        }
        display: flex;
        align-items: center;
        img {
          @media screen and (max-width: 768px) {
            margin-right: 0;
            margin-bottom: 20px;
          }
          width: 40px;
          margin-right: 20px;
        }
      }
      &:last-child {
        @media screen and (max-width: 768px) {
          margin-left: 0;
          text-align: center;
        }
        margin: 10px 0 0 60px;
      }
    }
  }
}
