.hometopbg {
  width: 1903px;
  height: 689px;
  margin: auto;
  background: url(../../assets/imgs/imgs/homecloud.png) center / contain
    no-repeat;
  position: relative;
  .text {
    width: 450px;
    color: white;
    font-size: 24px;
    line-height: 36px;
    position: absolute;
    left: 35%;
    top: 50%;
    transform: translate(-50%, -55%);
  }
}
.home-content-1 {
  width: 100%;
  height: 250px;
  background: #f0f0f0;
  > div {
    width: 1140px;
    height: 100%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .btn-wrap {
      width: 405px;
      height: 45px;
      background: #fff;
      border-radius: 100px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 5px 0 24px;
      input {
        width: 270px;
      }
      button {
        width: 93px;
        height: 39px;
        border-radius: 100px;
      }
    }
  }
}
button {
  text-align: center;
  vertical-align: middle;
  font-size: 14px;
  font-weight: bold !important;
  color: #fff;
  background: #ec407a;
  cursor: pointer;
  &:hover {
    opacity: 0.8;
  }
}
.home-content-2 {
  display: flex;
  justify-content: space-between;
  @media screen and (max-width: 768px) {
    flex-wrap: wrap;
  }
  > div {
    @media screen and (max-width: 768px) {
      width: 100%;
      margin-top: 55px;
      &:first-child {
        margin-top: 0;
      }
    }
    width: 260px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    > div,
    a {
      text-align: center;
    }
    .img-wrap {
      height: 55px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 20px;
      img {
        width: fit-content;
        height: 50px;
      }
    }
    .grey-color {
      margin: 10px 0 25px;
    }
    a {
      width: fit-content;
      margin: auto;
    }
  }
}

.home-content-4 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .homeAboutbg {
    width: 100%;
    height: 688.2px;
    position: relative;
    background: url(../../assets/imgs/imgs/Aboutcoffeecloud.png) center /
      contain no-repeat;
  }
  .btn-wrap {
    display: flex;
    align-items: center;
    button {
      width: 140px;
      height: 40px;
      border-radius: 50px;
      background: #f0f0f0;
      color: #ec407a;
    }
  }
}

.font-55 {
  font-size: 55px;
  line-height: 60px;
}

.enquire-btn {
  width: 100%;
  height: 40px;
  border-radius: 50px;
  background: #fff;
  color: #ec407a;
  margin-top: 50px;
}

.mobile-home-about {
  width: 100%;
  height: 350px;
  position: absolute;
  top: -330px;
}
