.highlight-points {
  width: 350px;
  margin: 0 0 60px;
  box-shadow: 0 0 10px 5px rgba(128, 128, 128, 0.2);
  padding: 45px;
  border-radius: 20px;

  .title {
    font-style: normal;
    font-weight: 900;
    font-size: 25px;
    line-height: 30px;
    color: #3f3f3f;
  }

  .points-container {
    margin: 35px 0 0;

    .point-row {
      margin: 0 0 15px;
      padding: 0 0 0 30px;
      background: url(../../assets/imgs/icon/vector.svg) no-repeat left 8px;
      background-size: 14px 8.69px;
      font-style: normal;
      font-weight: normal;
      font-size: 17px;
      line-height: 25px;
      color: #6f6f6f;
    }
  }

  @media (max-width: 768px) {
    display: block;
    width: 100%;
    margin: 0 auto 10px;

    .title {
      font-size: 20px;
      text-align: center;

      .points-container {
        .point-row {
          font-size: 14px;
        }
      }
    }
  }
}
